<template>
    <div class="cy_win">
        <div id="test"></div>
        <div class="line"></div>

<div class="codeArea">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;div id="test"&gt;&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import adModule from "cy_static/adModule";
    adModule("#test", {
        "keyId": "fqsc_banner",
        "column": "2",
        "rowSpace": "0",
        "colSpace": "0",
        "paddingTop": "0",
        "paddingBottom": "0",
        "unLazyNum": "2"
    });
&lt;/script&gt;
</xmp >
</div>

        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="line"></div>
        <div class="cy_example">
            <iframe ref="iframe" src="?t=2#/runcode"></iframe>
        </div>
    </div>
</template>

<script>
    import {trim} from "../../../assets/js/codeStyle";
    import adModule from "cy_static/adModule";
    export default {
        name: 'adModule ',
        data () {
            return {
                msg: 'adModule ',
                cmOptions:{
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true

                },
                cmOptions1:{
                        readOnly:"true",
                        theme : "darcula",
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true
                }
            }
        },
        mounted(){
            var m = this;  
            ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
                var dom, valDom, index = 0, getDom = function() {
                    index++;
                    dom = m.$refs[element + index];
                    valDom = m.$refs[element + "Val" + index];
                };
                getDom();
                while (dom) {
                    dom.codemirror.setValue(trim(valDom.innerHTML));
                    getDom();
                }
            });
            adModule("#test", {
                data : [{"id":"P000006320","sort":10,"adWord":"iphone11系列跳转","defaultShow":true,"adContent":"http://image.changyoyo.com/uploadimage/IMG20191115092508127.png","positionName":"分期商城顶部banner","usePlat":"H5","adUrl":"https://m.changyoyo.com/event/2018/activePlatform02/index.html?activityCode=02005700&urlOrder=0001&channel_source=02005700","adName":"iphone11系列跳转","adDesc":"iphone11系列跳转"},{"id":"P000005598","sort":1,"adWord":"中行分期免息","defaultShow":false,"adContent":"http://image.changyoyo.com/uploadimage/IMG20190731102618680.png","positionName":"分期商城顶部banner","usePlat":"H5","adUrl":"http://m.changyoyo.com/event/2017/1222/index.html","adName":"中行分期免息","adDesc":"中行分期免息"}],
                "keyId": "fqsc_banner",
                "column": "2",
                "rowSpace": "0",
                "colSpace": "0",
                "paddingTop": "0",
                "paddingBottom": "0",
                "unLazyNum": "2"
            });
        }, 
        methods:{
            runcode(){
                var m = this, frame = m.$refs.iframe;
                frame.src = "?a=" + Math.random() + "#/runcode"
                frame.onload = function() {
                    frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
                }

            }
        }
    }
</script>

<style scoped>    
    .cy_win{display:flex;flex-flow:column;flex:1}
    .codeArea{height:260px;overflow: hidden;}
    .cy_example{position:static;flex:10}  
</style>


